"use strict";

var modal = document.getElementById("js-newsletter-modal");
var close = document.getElementById("js-newsletter-close");

var DAYS_TO_HIDE = 30;

var cookieTime = localStorage.getItem("confete-hide-newsletter");

if (cookieTime == undefined || cookieTime == null) {
  cookieTime = 0;
}

var currentTime = new Date().getTime();
var popupShouldShow = (currentTime - cookieTime) / (1000 * 60 * 60 * 24) > DAYS_TO_HIDE;

var openModal = function openModal() {
  return modal.classList.add("active");
};

function closeModal() {
  modal.classList.remove("active");
  localStorage.setItem("confete-hide-newsletter", currentTime);
}

function windowOnClick(event) {
  if (event.target === modal) {
    closeModal();
  }
}

if (popupShouldShow) {
  openModal();
}

close.addEventListener("click", closeModal);
window.addEventListener("click", windowOnClick);