"use strict";

var searchTriggerDesktop = document.getElementById("js-header-search");
var searchTriggerMobile = document.getElementById("js-slide-out-search");
var searchDropdown = document.getElementById("search-dropdown");
var searchForm = document.getElementById("search_form");
var searchInput = searchForm.querySelector("input[type='search']");
var searchClose = document.getElementById("js-search-close");

var toggleSearch = function toggleSearch(event) {
  return searchDropdown.classList.toggle("active");
};

var focusInput = function focusInput() {
  setTimeout(function () {
    searchInput.focus();
  }, 300);
};

searchTriggerDesktop.addEventListener("click", function () {
  toggleSearch();
  focusInput();
});

searchTriggerMobile.addEventListener("click", function () {
  toggleSearch();
  focusInput();
});

searchClose.addEventListener("click", toggleSearch);